/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --orange: #f48915;
  --darkGray: #464d53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --appColor: #3c3f45;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}
.stroke-text {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}
.hero-buttons {
  display: flex;
  gap: 1rem;
}
button {
  cursor: pointer;
}
.btn {
  border: 4px solid transparent;
  font-weight: bold;
  padding: 0.5rem;
  transition: all 0.3s;
}
.blur {
  position: absolute;
  width: 26rem;
  height: 12rem;
  z-index: -1;
  border-radius: 50%;
  filter: blur(200px);
  background: rgba(253, 120, 43, 0.69);
}
